import React from "react";
import "./contact.css";
import { MdOutlineEmail, MdOutlineMessage } from "react-icons/md";
import { ImWhatsapp } from "react-icons/im";
import { useRef } from "react";
import emailjs from "@emailjs/browser";

const Contact = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_kynm3uq",
        "template_rwvuowi",
        form.current,
        "k3YAjHnHoat8eG_YL"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  };

  return (
    <section id="contact">
      <h5>Get In Touch</h5>
      <h2>Contact Me</h2>

      <div className="container contact__container">
        <div className="contact__options">
          <article className="contact__option">
            <MdOutlineEmail className="contact__option-icon" />
            <h4>Email</h4>
            <h5>elvinkhunt@gmail.com</h5>
            <a href="mailto:elvinkhunt@gmail.com">Send a Message</a>
          </article>

          <article className="contact__option">
            <MdOutlineMessage className="contact__option-icon" />
            <h4>Text Message</h4>
            <h5>+91 8238131411</h5>
            <a href="sms:+918238131411">Send a Message</a>
          </article>

          <article className="contact__option">
            <ImWhatsapp className="contact__option-icon" />
            <h4>Whatsapp</h4>
            <h5>+91 8238131411</h5>
            <a href="https://wa.me/918238131411">Send a Message</a>
          </article>
        </div>
        <form ref={form} onSubmit={sendEmail}>
          <input
            type="text"
            name="name"
            placeholder="Your Full name"
            required
          />
          <input type="email" name="email" placeholder="Your Email" required />
          <textarea
            name="message"
            rows="7"
            placeholder="Your Message"
          ></textarea>
          <button type="submit" className="btn btn-primary">
            Send Message
          </button>
        </form>
      </div>
    </section>
  );
};

export default Contact;
