import React from "react";
import "./header.css";
import CV from "../../assets/elvin_khunt_gcet_2023.pdf";

const CTA = () => {
  return (
    <div className="cta">
      <a href={CV} target="_blank" download className="btn">
        Download CV
      </a>
      <a href="#contact" className="btn btn-primary">
        Let's talk
      </a>
    </div>
  );
};

export default CTA;
