import React from "react";
import "./about.css";
import ME from "../../assets/elvin.png";
import { FaAward } from "react-icons/fa";
import { BsFileEarmarkCode } from "react-icons/bs";
import { VscFolderLibrary } from "react-icons/vsc";

const About = () => {
  return (
    <section id="about">
      <h5>Get To Know</h5>
      <h2>About Me</h2>

      <div className="container about__container">
        <div className="about__me">
          <div className="about__me-image">
            <img src={ME} alt="About" />
          </div>
        </div>

        <div className="about__content">
          <div className="about__cards">
            <article className="about__card">
              <FaAward className="about__icon" />
              <h5>Internships</h5>
              <small>3 Internships</small>
            </article>

            <article className="about__card">
              <BsFileEarmarkCode className="about__icon" />
              <h5>Competitive Programming</h5>
              <small>200+ Problems Solved</small>
            </article>

            <article className="about__card">
              <VscFolderLibrary className="about__icon" />
              <h5>Projects</h5>
              <small>10+ Completed Projects</small>
            </article>
          </div>
          <p>
            An enthusiastic and curious programmer looking for a responsible
            position in a reputable organization to expand my learnings,
            knowledge and skills. A self-starter and capable to use technical
            skills for betterment of the organization.
          </p>
          <a href="#contact" className="btn btn-primary">
            Let's Talk
          </a>
        </div>
      </div>
    </section>
  );
};

export default About;
